.pagination {
	border: solid 1px $mute-border-color;
	background: #fff;
	display: inline-block;
	padding: 0;
	margin: 0;
	@include radius;
	@include clearfix;

	li {
		display: block;
		float: left;
		height: 40px;
		width: 40px;
		line-height: 40px;
		text-align: center;


		&.disable {
			background: $mute-color;
			color: darken($mute-border-color, 10%);
			cursor: no-drop; }

		a {
			display: block;
			color: $primary-color;
			text-decoration: none; }

		&:not(.disable):hover,
		&:not(.disable).active {
			background: $primary-color;

			a,span {
				color: $mute-color; } }
		&:not(.disable).active:hover {
			cursor: no-drop; } } }
