@mixin media-up($name) {
	$size: '';
	@if is-number($name) {
		$size: $name; }
	@else {
		$size: map-get($_breakpoints, $name); }
	@media screen and (min-width: $size) {
		@content; } }


@mixin media-down($name) {
	$size: '';
	@if is-number($name) {
		$size: $name; }
	@else {
		$size: map-get($_breakpoints, $name); }
	@media screen and (max-width: $size) {
		@content; } }

@mixin media-between($size1, $size2) {
	$value1: map-get($_breakpoints, $size1);
	$value2: map-get($_breakpoints, $size2);
	@media (min-width: $value1) and (max-width: $value2) {
		@content; } }


@mixin media-custom($direction, $value) {
	@media screen and (#{$direction}-width: $value) {
		@content; } }


@mixin is--mobile($direction: max, $value: $mobile__view) {
	@include media-custom($direction, $value) {
		@content; } }

@mixin is--tablet($direction: max, $value: $tablet_view) {
	@include media-custom($direction, $value) {
		@content; } }

@mixin is--desktop($direction: min, $value: $mobile__view) {
	@include media-custom($direction, $value) {
		@content; } }
